import { Icons } from '@/components/icons';

export function Loading() {
  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-gray-100 to-gray-200 dark:from-gray-900 dark:to-gray-800">
      <div className="relative">
        <div className="absolute -inset-4 bg-white dark:bg-gray-800 rounded-2xl shadow-neu dark:shadow-neu-dark"></div>
        <div className="relative bg-white dark:bg-gray-800 rounded-xl p-8 shadow-neu-inner dark:shadow-neu-inner-dark">
          <div className="flex flex-col items-center space-y-4">
            <div className="relative">
              <div className="absolute -inset-4 bg-white dark:bg-gray-800 rounded-full shadow-neu dark:shadow-neu-dark"></div>
              <div className="relative bg-white dark:bg-gray-800 rounded-full p-4 shadow-neu-inner dark:shadow-neu-inner-dark">
                <Icons.spinner className="h-8 w-8 animate-spin text-blue-500 dark:text-blue-400" />
              </div>
            </div>
            <p className="text-gray-600 dark:text-gray-300 font-medium">Loading...</p>
          </div>
        </div>
      </div>
    </div>
  );
} 